import React from 'react'
import { Link } from 'gatsby'
import Img from "gatsby-image"
import ReactMarkdown from "react-markdown"

const Article = ({ data }) => {
    console.log(data)
    return (
        <div className="articles-item">
            <Img className="articles-item__img"  fixed={data.image.childImageSharp.fixed} />
            <div className="articles-item__content">
                <h3 className="articles-item__title">
                    <Link className="articles-item__link" to={`/${data.id}`}>{data.title}</Link>
                </h3>
                <i><strong></strong>{data.published_at}</i>
                <i><strong>Autor: </strong>{data.author ? data.author.username : 'Anónimo'}</i>
            </div>
            {/* <div className="articles-item__content">
                <ReactMarkdown
                    source={data.content}
                    escapeHtml={false}
                />
            </div> */}
            
        </div>
        
    )

}

export default Article